<template>
    <!-- 同理喜爱收藏likeMovie页面 -->
    <div class="tickets">
        <header>
          <van-nav-bar left-text="返回" title="影票详情" left-arrow :border="false" @click-left="back">/></van-nav-bar>
        </header>

        <div class="ticketsbox">
            <div class="box">
                <div class="tickets_info">
                    <div class="movieData clearfix">
                        <div class="fl movieInfo">
                            <div class="movieTitle one-txt">{{ticketsData.title}}</div>
                            <div class="tag">5号厅</div>
                            <div class="seatbox">
                                <div>座位号：</div> 
                                <div class="seat">
                                    <div class="tickets">
                                        <span>1排2座</span>
                                        <span>1排2座</span>
                                        <span>1排2座</span>
                                        <span>1排2座</span>
                                        <span>1排2座</span>
                                    </div>
                                </div>
                            </div>
                            <div class="time">2020-01-25 17:05~18:30</div>
                        </div>
                        <div class="fr movieImg">
                            <img class="autoimg" :src="ticketsData.images.small" alt=""> 
                        </div>
                    </div>
                    <div class="left_circle"></div>
                    <div class="right_circle"></div>
                </div>
                <div class="QR_code">
                    <div class="title">1张电影票</div>
                    <div class="codeImg"></div>
                    <div class="num">
                        <div>取票号：11111</div>
                    </div>
                    <div class="left_circle"></div>
                    <div class="right_circle"></div>
                </div>
                <div class="address clearfix">
                    <div class="fl one-txt title">
                        <span>万达影院(天河区)</span>
                        <span>&nbsp; 天河区珠吉路69号</span>
                    </div>
                    <div class="fr icon">
                        <i></i>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {createNamespacedHelpers} from 'vuex';
    const {mapState, mapMutations} = createNamespacedHelpers('ticketsModule');
    export default {
        name:'tickets',
        
        computed: {
          ...mapState(['ticketsData',])
        },

        methods:{

            // 返回上一步
            back() {
                this.$router.go(-1);
            },

            // 票根数据
            getTicketsData(id){
                //开启加载提示
                this.$toast.loading({
                  duration: 0,
                  message: '加载中...'
                });

                this.axios({
                    method:'GET',
                    url:'https://douban.uieee.com/v2/movie/subject/' + id
                }).then(result =>{
                    // console.log('result=>',result);
                    this.$store.commit('ticketsModule/getTicketsData',result.data);

                    this.$toast.clear();
                }).catch(err=>{
                    this.$toast.clear();
                })
            }
        },


        created(){
            // console.log(this.$route);
            let Id = this.$route.query.id;
            // console.log('Id=>',Id);

            // 验证登录
            let userStatus = localStorage.getItem('user');
            // console.log(userStatus); //Yes:{"isLogin":true,"username":"wasd"}

            if(userStatus){
                let userInfo = JSON.parse(userStatus);
                // console.log(userInfo); //序列化之后：{isLogin: true, username: "wasd"}

                if(userInfo.isLogin){

                    // 获取电影票更数据
                    this.getTicketsData(Id);
                    // 生成页面数据

                }else{
                    this.$router.push({name: 'login'}); //未登录
                }
            }else{
                this.$router.push({name: 'login'});//未登录
            }
        }
    }
</script>

<style lang="less" scoped>
    .tickets{
        header{
            width: 100%;
            background-color: #697A9C;

            /deep/.van-nav-bar{
              background-color: transparent;
            }
            .van-nav-bar__text{
              color: #fff;
              font-size: 16px;
            }
            .van-nav-bar__title{
                color: #fff;
                font-size: 18px;
            }
            .van-icon-arrow-left:before{
               color: #fff;
               font-size: 20px;
            }

            /deep/.van-icon{
              padding: 0 6px;
            }
        }

        .ticketsbox{
            position: fixed;
            top:47px;
            bottom:0;
            left: 0;
            right: 0;
            padding: 0 10px;
            overflow-y: auto;
            box-shadow: 2px 2px 1px 4px #ddd;
        }

        .tickets_info{
            margin: 15px 15px 0;
            padding:20px;
            background-color: #fff;
            border: 1px solid #f5f5f5;
            border-bottom: 1px dashed #666;
            position: relative;
            .movieData{
                padding: 10px 0;
                .movieInfo{
                    width: calc(~"100% - 90px");
                    .movieTitle{
                        font-size: 18px;
                        font-weight: bold;
                        padding: 2px 0;
                    }
                    .actor{
                        font-size: 14px;
                        line-height: 24px;
                        color: #999;
                    }
                    .tag{
                        font-size:16px;
                        margin:10px 0;
                        color:#666;
                    }
                }

                .movieImg{
                    width: 80px;
                    border: 1px solid #697A9C;
                }

                .seatbox{
                    font-size:16px;
                    margin:10px 0;
                    color:#666;
                    >.seat{
                        height: 24px;
                        overflow-x: auto;
                        overflow-y: hidden;
                        margin:4px 0;
                        padding-bottom: 6px;
                        border-bottom:1px solid #697A9C;
                    }
                    .tickets{
                        width:300px;
                        height: 24px;
                        >span{
                            padding: 4px 6px;
                        }
                    }
                }

                .time{
                    font-size: 14px;
                    color: #FF3472;
                }

            }

            .left_circle{
                width: 20px;
                height: 20px;
                background-color: #fff;
                border-radius: 4%;
                position: absolute;
                left: -10px;
                bottom: -10px;
                z-index: 5;
            }
            .right_circle{
                width: 20px;
                height: 20px;
                background-color: #fff;
                border-radius: 50%;
                position: absolute;
                right: -10px;
                bottom: -10px;
                z-index: 5;

            }
        }

        .QR_code{
            margin: 0 15px;
            padding:20px;
            background-color: #fff;
            position: relative;
            border:1px solid #ddd;
            text-align: center;
            border-bottom: 1px dashed #666;

            .title{
                padding-bottom: 10px;
                font-size: 14px;
            }
            .codeImg{
                width: 120px;
                height: 120px;
                background: #ddd;
                margin: 0 auto;
            }
            .num{
                margin-top: 8px;
                >div{
                    padding: 2px 0;
                    text-align: center;
                    font-size:14px;
                    color:#666;
                }
            }
            .left_circle{
                width: 20px;
                height: 20px;
                background-color: #fff;
                border-radius: 50%;
                position: absolute;
                left: -10px;
                bottom: -10px;
                z-index: 5;

            }
            .right_circle{
                width: 20px;
                height: 20px;
                background-color: #fff;
                border-radius: 50%;
                position: absolute;
                right: -10px;
                bottom: -10px;
                z-index: 5;

            }
        }

        .address{
            margin: 0 15px;
            padding:20px;
            background-color: #fff;
            position: relative;
            border:1px solid #ddd;

            >div{
                font-size: 16px;
                color: #333;
            }
            .title{
                width: calc(~"100% - 80px");
                >span{
                    display: block;
                    line-height: 30px;
                    &:first-child{
                        font-size: 18px;
                        color:#000;
                        font-weight: bold;
                    }
                }
            }

            .icon{
                width: 70px;
                height:60px;
                background-color: #fff;
                box-shadow: 1px 2px 1px 0px #ddd;
                >i{
                    display: block;
                    width: 60px;
                    height: 60px;
                    background: url("../assets/images/daohang.png") no-repeat center center;
                    background-size: 30px 30px;
                }
            }
        }

    }
</style>